* {
  box-sizing: border-box;
}

html, body {
  width: 100vw;
  height: 100vh;
  margin: 0;
  padding: 0;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 1.1em;
  overflow: hidden auto;
}

body {
  -webkit-user-select: none;
  user-select: none;
  background-color: #f5f5f9;
}

.chat-container {
  height: 500px;
  margin: 0;
  padding: 0;
  overflow: auto;
  height: 100% !important;
}

.chat-message {
  background-color: #fff;
  border-radius: 10px;
  margin: 0 0 1.1rem;
  padding: 19px 5px 7px;
  position: relative;
}

.message-buttons {
  float: right;
  border: none;
  border-radius: 4px;
  margin: 0;
  padding: 0;
  position: relative;
  top: -19px;
  right: 5px;
}

.message-button {
  cursor: pointer;
  border: none;
  border-radius: 4px;
  margin: 1px;
  padding: 0 2px;
  position: relative;
}

.message-button:hover {
  top: 2px;
  left: 2px;
}

.markdown-editor {
  cursor: text;
  margin: 0;
  padding: 0;
  display: flex;
}

.markdown-editor .textarea-container {
  float: left;
  margin: 0;
  padding: 0;
  position: relative;
}

.markdown-editor textarea {
  width: 95vw;
  max-width: 95vw;
  font-size: 100%;
}

.markdown-editor .editor-buttons {
  z-index: 14;
  position: absolute;
  top: 0;
  right: 5px;
}

.chat-message .message-text {
  -webkit-user-select: text;
  user-select: text;
  margin: 0;
  padding: 0;
}

.chat-message .message-text p {
  white-space: pre-wrap;
  margin: 1px;
  padding: 1px;
  font-size: .9rem;
}

.chat-message .message-text p code {
  background-color: #f5f5f9;
  border-radius: 2.5px;
  padding: 1.4px 1.2px;
  font-size: 82%;
}

.inline-container {
  z-index: 10;
  background-color: #0000;
  width: 60%;
  height: 50px;
  margin: 4px;
  padding: 0;
  position: absolute;
  pointer-events: all !important;
}

#terminal {
  z-index: 10;
  background-color: #0000;
  width: 80%;
  height: 50px;
  margin: 4px;
  padding: 0;
  position: absolute;
  pointer-events: all !important;
}

.output-container {
  border-top: 1.5px solid #000;
  margin-top: 10px;
  padding-top: 2px;
  display: none;
}

.output-container:before {
  font-variant: small-caps;
  content: attr(x-output-tag);
  font-family: monospace;
}

.chat-message .message-text pre {
  white-space: pre-wrap;
  background-color: #f5f5f9;
  border-radius: 2px;
  max-width: 95vw;
  margin-top: 7px;
  margin-bottom: 10px;
  margin-left: 10px;
  font-size: .89rem;
}

.hljs {
  padding: .5em !important;
}

.code-buttons {
  float: right;
  border: none;
  border-radius: 4px;
  height: 40px;
  min-height: 40px;
  margin: 0;
  padding: 0;
  position: relative;
  top: 5px;
  right: 5px;
}

.code-button {
  cursor: pointer;
  border: none;
  border-radius: 4px;
  margin: 1px;
  padding: 0 2px;
  position: relative;
}

.code-button:hover {
  top: 2px;
  left: 2px;
}

@keyframes fadein {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
    display: block;
  }
}

@keyframes fadeout {
  from {
    opacity: 1;
  }

  to {
    opacity: 0;
    display: none !important;
  }
}

.notification {
  float: inline-end;
  white-space: nowrap;
  background-color: #fdfdfd;
  border: .9px solid #d9d9d9;
  border-radius: 3px;
  margin: 0;
  padding: 2px;
  font-family: Arial, Helvetica, sans-serif;
  font-size: .5rem;
  line-height: .5rem;
  animation: 1s fadein;
  display: block;
  position: absolute;
  top: 2.8em;
  right: 0;
}

.chat-message-self {
  background-color: #dcf8c6;
}

.chat-message .username {
  -webkit-user-select: text;
  user-select: text;
  color: silver;
  font-size: 12px;
  position: absolute;
  top: 0;
  bottom: -20px;
  left: 2px;
}

.chat-message .timestamp {
  color: #999;
  font-size: 12px;
  position: absolute;
  bottom: -10px;
  right: 0;
}

pre code.hljs {
  padding: 1em;
  display: block;
  overflow-x: auto;
}

code.hljs {
  padding: 3px 5px;
}

.hljs {
  color: #444;
  background: #f3f3f3;
}

.hljs-comment {
  color: #697070;
}

.hljs-tag, .hljs-punctuation {
  color: #444a;
}

.hljs-tag .hljs-name, .hljs-tag .hljs-attr {
  color: #444;
}

.hljs-keyword, .hljs-attribute, .hljs-selector-tag, .hljs-meta .hljs-keyword, .hljs-doctag, .hljs-name {
  font-weight: bold;
}

.hljs-type, .hljs-string, .hljs-number, .hljs-selector-id, .hljs-selector-class, .hljs-quote, .hljs-template-tag, .hljs-deletion {
  color: #800;
}

.hljs-title, .hljs-section {
  color: #800;
  font-weight: bold;
}

.hljs-regexp, .hljs-symbol, .hljs-variable, .hljs-template-variable, .hljs-link, .hljs-selector-attr, .hljs-operator, .hljs-selector-pseudo {
  color: #ab5656;
}

.hljs-literal {
  color: #695;
}

.hljs-built_in, .hljs-bullet, .hljs-code, .hljs-addition {
  color: #397300;
}

.hljs-meta {
  color: #1f7199;
}

.hljs-meta .hljs-string {
  color: #38a;
}

.hljs-emphasis {
  font-style: italic;
}

.hljs-strong {
  font-weight: bold;
}

